<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="notice" ref="noticeForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="notice.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="notice_picture" v-model="notice.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="notice.title" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="categoryId">
          <el-select v-model="notice.categoryId" placeholder="" :disabled="dialog.disabled">
            <el-option v-for="category in categoryDict" :key="category.id" :value="category.id" :label="category.name"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="publishTime">
          <el-date-picker v-model="notice.publishTime" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
        </el-form-item>
        <el-form-item label="" prop="content">
          <sui-editor v-model="notice.content" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "noticeForm",
    data() {
      return {
        categoryDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        notice: {
          id: "",
          picture: "",
          title: "",
          categoryId: "",
          publishTime: "",
          content: ""
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'blur'}],
          title: [{required: true, message: '', trigger: 'blur'},{validator: this.$validator.unique,form:this,model:'notice',tableName:'notice',column:'title',message: '', trigger: 'blur'}],
          categoryId: [{required: true, message: '', trigger: 'blur'}],
          publishTime: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
    },
    methods: {
      //
      getCategoryDict() {
        this.$request.doGet('/admin/category/getList').then(res => {
          if (res.isOk) {
            this.categoryDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getCategoryDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getNotice(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getNotice(id) {
        this.$request.doGet("/admin/notice/get?id=" + id).then(res => {
          if (res.isOk) {
            this.notice = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.noticeForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/notice/sub', that.notice).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.noticeForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



