<!--  -->
<template>
  <div class="body">
    <!--  -->
    <sysAuthor-form ref="sysAuthorDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="nickName">
        <el-input v-model="queryForm.nickName" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="gender">
        <el-select v-model="queryForm.gender" placeholder="" size="small" clearable>
          <el-option v-for="dict in genderDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="openid">
        <el-input v-model="queryForm.openid" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['sysAuthor:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysAuthor:delete']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-export type="primary" size="mini" action="/admin/sysAuthor/export" title="" v-hasPermi="['sysAuthor:export']"/>
      </el-col>
      <el-col :span="1.5">
        <el-import type="primary" size="mini" action="/admin/sysAuthor/import" title="" v-hasPermi="['sysAuthor:import']"/>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="avatarUrl" sortable>
        <template slot-scope="scope">
          <el-image :src="formatUrl(scope.row.avatarUrl)" class="column-image"></el-image>
        </template>
      </el-table-column>
<!--      <el-table-column label="" prop="nickName" sortable/>-->
<!--      <el-table-column label="" prop="gender" sortable>-->
<!--        <template slot-scope="scope">-->
<!--          <span v-for="dict in genderDict" v-if="dict.dictValue==scope.row.gender">{{dict.dictLabel}}</span>-->
<!--        </template>-->
<!--      </el-table-column>-->
      <el-table-column label="" prop="country" sortable/>
      <el-table-column label="" prop="province" sortable/>
      <el-table-column label="" prop="city" sortable/>
<!--      <el-table-column label="" prop="openid" sortable/>-->
<!--      <el-table-column label="" prop="unionid" sortable/>-->
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysAuthor:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysAuthor:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysAuthor:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysAuthorForm from './authorForm'
    export default {
      components: {sysAuthorForm},
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            avatarUrl: "",
            nickName: "",
            gender: "",
            country: "",
            province: "",
            city: "",
            openid: "",
            unionid: ""
          }
        };
      },
      mounted() {
        //
        this.getList();
      },
      computed: {
        //
        genderDict() {
          return this.$store.getters.getDictArray('sys_sex');
        },
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doGet('/admin/sysAuthor/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //
        openDialog(option) {
          this.$refs.sysAuthorDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              that.selectedData.forEach(function (item, index) {
                that.delete(item.id);
              });
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.delete(id);
          });
        },
        //
        delete(id) {
          this.$request.doGet('/admin/sysAuthor/delete?id=' + id).then(res => {
            if (res.isOk) {
              this.getList();
              this.showSuccess("");
            } else {
              this.showWarn(res.info);
            }
          });
        },
      }
    };
</script>



